.shower.full {
  background-color: #111;
}
.slide::after {
  color: #1471b6;
  background: none;
  height: 75px;
  right: 20px;
}
.slide::before {
  position: absolute;
  top: 75px;
  right: 100px;
  height: 50px;
}
.slide {
  padding-top: 60px;
  padding-left: 60px;
}
.slide h2 {
  color: #1471b6;
}
.hightlight,
.important,
.alert {
  border-radius: 0.2em;
  padding: 0.1em 0.3em;
}
.hightlight {
  background: #fafaa2;
}
.hightlight * {
  color: #000 !important;
}
.important {
  background: #a11212;
  color: #fff;
}
.important * {
  color: #fff !important;
}
.alert {
  background: #d10a0a;
  color: #fff;
}
.alert * {
  color: #fff !important;
}
.comment {
  color: #999;
}
.progress {
  border-color: #1471b6 rgba(0,0,0,0) #1471b6 #1471b6;
}
.tag {
  color: #ad1531;
}
.attr {
  color: #4f7302;
}
.slide p.center {
  text-align: center;
}
.slide p.center img {
  max-width: 100%;
}
.slide p.quote {
  text-align: center;
  font-style: italic;
}
blockquote {
  font-size: 35px;
  line-height: 1.4;
}
figcaption {
  opacity: 0.5;
  position: relative;
  line-height: 1.6;
  margin-top: 40px !important;
}
